// app/projects/[projectId]/page.js
"use client";

import React, { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import {
  Plus,
  Edit,
  Trash2,
  X,
  Maximize2,
  ArrowLeft,
  ChevronRight,
  User,
  Calendar,
  Bug,
  FileText,
  Home,
  MessageSquare,
  AlertCircle,
} from "lucide-react";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Sheet, SheetContent, SheetHeader, SheetTitle } from "@/components/ui/sheet";
import { Dialog, DialogContent, DialogHeader } from "@/components/ui/dialog";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";

export default function ProjectPage() {
  const router = useRouter();
  const { projectId, slug = [] } = useParams();
  const slugArray = Array.isArray(slug) ? slug : [slug];

  const module = slugArray[0] || "overview";
  const itemId = slugArray[1] ? Number(slugArray[1]) : null;
  const action = slugArray[2]; // "create" | "edit" | undefined

  // Data
  const [tasks, setTasks] = useState([
    { id: 1, title: "Design dashboard", description: "Create new analytics dashboard", status: "In Progress", assignee: "Alice", dueDate: "2025-12-01" },
    { id: 2, title: "Fix login bug", description: "Users can't login with Google", status: "Open", assignee: "Bob", dueDate: null },
  ]);
  const [bugs, setBugs] = useState([
    { id: 101, title: "Button not clickable on mobile", severity: "High", status: "Open", assignedTo: "Carol" },
    { id: 102, title: "Typo in footer", severity: "Low", status: "Closed", assignedTo: "Dave" },
  ]);
  const [reports, setReports] = useState([
    { id: 201, name: "Q4 Progress Report", type: "quarterly", createdBy: "Eve" },
  ]);

  // Sheet & Fullscreen state
  const [sheetOpen, setSheetOpen] = useState(false);
  const [isFullScreen, setIsFullScreen] = useState(false);
  const [sheetMode, setSheetMode] = useState("view"); // "view" | "44" | "edit"
  const [currentItem, setCurrentItem] = useState(null);

  // Sync URL with Sheet
  useEffect(() => {
    if (itemId || action) {
      setSheetOpen(true);
      setIsFullScreen(false);

      if (action === "create") {
        setSheetMode("create");
        setCurrentItem(null);
      } else if (action === "edit" && itemId) {
        setSheetMode("edit");
        const item = getItemById(module, itemId);
        setCurrentItem(item);
      } else if (itemId) {
        setSheetMode("view");
        const item = getItemById(module, itemId);
        setCurrentItem(item);
      }
    } else {
      setSheetOpen(false);
      setCurrentItem(null);
      setSheetMode("view");
    }
  }, [module, itemId, action]);

  const getItemById = (mod, id) => {
    if (mod === "task") return tasks.find(t => t.id === id);
    if (mod === "bug") return bugs.find(b => b.id === id);
    if (mod === "report") return reports.find(r => r.id === id);
    return null;
  };

  const closeSheet = () => {
    router.push(`/projects/${projectId}/${module}`);
  };

  const saveItem = (data) => {
    if (sheetMode === "create") {
      if (module === "task") {
        const newId = Math.max(...tasks.map(t => t.id), 0) + 1;
        setTasks(prev => [{ id: newId, ...data }, ...prev]);
        router.push(`/projects/${projectId}/task/${newId}`);
      }
      if (module === "bug") {
        const newId = Math.max(...bugs.map(b => b.id), 0) + 1;
        setBugs(prev => [{ id: newId, ...data }, ...prev]);
        router.push(`/projects/${projectId}/bug/${newId}`);
      }
      if (module === "report") {
        const newId = Math.max(...reports.map(r => r.id), 0) + 1;
        setReports(prev => [{ id: newId, ...data }, ...prev]);
        router.push(`/projects/${projectId}/report/${newId}`);
      }
    } else if (sheetMode === "edit" && currentItem) {
      if (module === "task") setTasks(prev => prev.map(t => t.id === currentItem.id ? { ...t, ...data } : t));
      if (module === "bug") setBugs(prev => prev.map(b => b.id === currentItem.id ? { ...b, ...data } : b));
      if (module === "report") setReports(prev => prev.map(r => r.id === currentItem.id ? { ...r, ...data } : r));
      router.push(`/projects/${projectId}/${module}/${currentItem.id}`);
    }
  };

  const deleteItem = () => {
    if (!currentItem) return;
    if (module === "task") setTasks(prev => prev.filter(t => t.id !== currentItem.id));
    if (module === "bug") setBugs(prev => prev.filter(b => b.id !== currentItem.id));
    if (module === "report") setReports(prev => prev.filter(r => r.id !== currentItem.id));
    closeSheet();
  };

  const openCreate = () => router.push(`/projects/${projectId}/${module}/create`);
  const openEdit = () => router.push(`/projects/${projectId}/${module}/${itemId}/edit`);

  return (
    <>
      <div className="flex h-screen bg-gray-50">
        {/* Main Content */}
        <div className="flex-1 overflow-y-auto">
          <div className="p-8">
            <h1 className="text-3xl font-bold mb-8">Project {projectId}</h1>

            <Tabs value={module} onValueChange={(v) => router.push(`/projects/${projectId}/${v}`)}>
              <TabsList className="grid w-full max-w-md grid-cols-4 mb-8">
                <TabsTrigger value="overview"><Home className="w-4 h-4 mr-2" /> Overview</TabsTrigger>
                <TabsTrigger value="task">Tasks ({tasks.length})</TabsTrigger>
                <TabsTrigger value="bug"><Bug className="w-4 h-4 mr-2" /> Bugs ({bugs.length})</TabsTrigger>
                <TabsTrigger value="report"><FileText className="w-4 h-4 mr-2" /> Reports</TabsTrigger>
              </TabsList>

              <TabsContent value="overview" className="mt-8">
                <Card className="p-8 text-center text-gray-600">
                  <h2 className="text-2xl font-semibold mb-4">Welcome to Project {projectId}</h2>
                  <p>Select a module from the tabs above to get started.</p>
                </Card>
              </TabsContent>

              {/* TASKS LIST */}
              <TabsContent value="task" className="space-y-4">
                <div className="flex justify-between items-center mb-6">
                  <h2 className="text-2xl font-semibold">Tasks</h2>
                  <Button onClick={openCreate}><Plus className="w-4 h-4 mr-2" />New Task</Button>
                </div>
                {tasks.map(task => (
                  <Card
                    key={task.id}
                    className="p-5 cursor-pointer hover:shadow-lg transition-all"
                    onClick={() => router.push(`/projects/${projectId}/task/${task.id}`)}
                  >
                    <div className="flex justify-between items-start">
                      <div>
                        <h3 className="text-lg font-semibold">{task.title}</h3>
                        <div className="flex items-center gap-4 mt-2 text-sm text-gray-600">
                          <Badge variant={task.status === "Open" ? "secondary" : task.status === "In Progress" ? "default" : "outline"}>
                            {task.status}
                          </Badge>
                          <span className="flex items-center gap-1"><User className="w-4 h-4" />{task.assignee}</span>
                          {task.dueDate && <span className="flex items-center gap-1"><Calendar className="w-4 h-4" />Nov 20</span>}
                        </div>
                      </div>
                      <ChevronRight className="w-5 h-5 text-gray-400" />
                    </div>
                  </Card>
                ))}
              </TabsContent>

              {/* BUGS & REPORTS similar - omitted for brevity but same pattern */}
            </Tabs>
          </div>
        </div>

        {/* SLIDING SHEET - View / Create / Edit */}
        <Sheet open={sheetOpen && !isFullScreen} onOpenChange={(o) => !o && closeSheet()}>
          <SheetContent className="w-full sm:max-w-3xl p-0">
            <SheetHeader className="sticky top-0 bg-white border-b z-10 px-8 py-6 flex items-center justify-between">
              <SheetTitle className="text-2xl font-bold pr-8">
                {sheetMode === "create" && `New ${module}`}
                {sheetMode === "edit" && `Edit ${module}`}
                {sheetMode === "view" && currentItem?.title || currentItem?.name || "Details"}
              </SheetTitle>
              <div className="flex items-center gap-2">
                <Button variant="ghost" size="icon" onClick={() => setIsFullScreen(true)}>
                  <Maximize2 className="w-5 h-5" />
                </Button>
                <Button variant="ghost" size="icon" onClick={closeSheet}>
                  <X className="w-5 h-5" />
                </Button>
              </div>
            </SheetHeader>

            <div className="p-8 space-y-8 overflow-y-auto">
              {/* VIEW MODE */}
              {sheetMode === "view" && currentItem && module === "task" && (
                <>
                  <div className="grid grid-cols-2 gap-8">
                    <div>
                      <Label className="text-gray-600">Assignee</Label>
                      <p className="text-xl font-medium mt-2">{currentItem.assignee}</p>
                    </div>
                    <div>
                      <Label className="text-gray-600">Status</Label>
                      <p className="text-xl font-medium mt-2">{currentItem.status}</p>
                    </div>
                  </div>
                  <Separator />
                  <div>
                    <h3 className="text-xl font-semibold mb-3">Description</h3>
                    <p className="text-gray-700 leading-relaxed">{currentItem.description}</p>
                  </div>
                  <div className="flex gap-3 pt-6">
                    <Button onClick={openEdit}><Edit className="w-4 h-4 mr-2" />Edit Task</Button>
                    <Button variant="destructive" onClick={deleteItem}><Trash2 className="w-4 h-4 mr-2" />Delete</Button>
                  </div>
                </>
              )}

              {/* EDIT / CREATE FORM (Task Example) */}
              {(sheetMode === "create" || sheetMode === "edit") && module === "task" && (
                <form onSubmit={(e) => { e.preventDefault(); const fd = new FormData(e.target); saveItem(Object.fromEntries(fd)); }} className="space-y-6">
                  <div>
                    <Label>Title</Label>
                    <Input name="title" defaultValue={currentItem?.title || ""} required className="mt-2" />
                  </div>
                  <div>
                    <Label>Description</Label>
                    <Textarea name="description" defaultValue={currentItem?.description || ""} className="min-h-40 mt-2" />
                  </div>
                  <div className="grid grid-cols-2 gap-6">
                    <div>
                      <Label>Status</Label>
                      <Select name="status" defaultValue={currentItem?.status || "Open"}>
                        <SelectTrigger className="mt-2"><SelectValue /></SelectTrigger>
                        <SelectContent>
                          <SelectItem value="Open">Open</SelectItem>
                          <SelectItem value="In Progress">In Progress</SelectItem>
                          <SelectItem value="Done">Done</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label>Assignee</Label>
                      <Input name="assignee" defaultValue={currentItem?.assignee || ""} className="mt-2" />
                    </div>
                  </div>
                  <div className="flex justify-end gap-3 pt-6">
                    <Button type="button" variant="outline" onClick={closeSheet}>Cancel</Button>
                    <Button type="submit">Save Task</Button>
                  </div>
                </form>
              )}
            </div>
          </SheetContent>
        </Sheet>

        {/* FULL SCREEN DIALOG */}
        <Dialog open={isFullScreen} onOpenChange={setIsFullScreen}>
          <DialogContent className="max-w-6xl h-[92vh] p-0">
            <DialogHeader className="sticky top-0 bg-white border-b z-10 px-8 py-6 flex items-center gap-4">
              <Button variant="ghost" size="icon" onClick={() => setIsFullScreen(false)}>
                <ArrowLeft className="w-6 h-6" />
              </Button>
              <h1 className="text-3xl font-bold flex-1">
                {sheetMode === "create" ? `New ${module}` : currentItem?.title || "Task Details"}
              </h1>
              <Button variant="ghost" size="icon" onClick={() => { setIsFullScreen(false); closeSheet(); }}>
                <X className="w-6 h-6" />
              </Button>
            </DialogHeader>
            <div className="p-10">
              <p className="text-xl text-gray-600">Full-screen mode active â€” perfect for long forms, attachments, comments, etc.</p>
            </div>
          </DialogContent>
        </Dialog>
      </div>
    </>
  );
}





